<template>
	<view class="login-container">
		<!-- È°∂ÈÉ®ÂØºËà™Ê†è -->
		<view class="login-header" :style="{ paddingTop: customNavBarHeight + 'px' }">
			<view class="nav-back" @click="goBack">
				<text class="back-icon">‚Üê</text>
			</view>
			<text class="nav-title">ÁôªÂΩï</text>
			<view class="nav-placeholder"></view>
		</view>

		<!-- ÁôªÂΩïÂÜÖÂÆπÂå∫Âüü -->
		<view class="login-content">
			<!-- LogoÂíåÊ†áÈ¢ò -->
			<view class="login-logo">
				<image src="../../static/logo.png" class="logo-img" mode="aspectFit"></image>
				<text class="logo-title">ÂÆ†Áâ©ÂïÜÂüé</text>
				<text class="logo-subtitle">‰∏∫Áà±ÂÆ†Êèê‰æõÊúÄÂ•ΩÁöÑÁîüÊ¥ª</text>
			</view>

			<!-- ÁôªÂΩïÊñπÂºèÂàáÊç¢ -->
			<view class="login-tabs">
				<view 
					class="tab-item" 
					:class="{ active: loginType === 'phone' }" 
					@click="switchLoginType('phone')"
				>
					<text>ÊâãÊú∫Âè∑ÁôªÂΩï</text>
				</view>
				<view 
					class="tab-item" 
					:class="{ active: loginType === 'wechat' }" 
					@click="switchLoginType('wechat')"
				>
					<text>ÂæÆ‰ø°ÁôªÂΩï</text>
				</view>
			</view>

			<!-- ÊâãÊú∫Âè∑ÁôªÂΩïË°®Âçï -->
			<view v-if="loginType === 'phone'" class="login-form">
				<view class="form-item">
					<text class="form-label">ÊâãÊú∫Âè∑</text>
					<input 
						type="number" 
						v-model="phone" 
						class="form-input" 
						placeholder="ËØ∑ËæìÂÖ•ÊâãÊú∫Âè∑"
						maxlength="11"
					/>
				</view>
				
				<view class="form-item">
					<text class="form-label">È™åËØÅÁ†Å</text>
					<view class="code-input-wrapper">
						<input 
							type="number" 
							v-model="code" 
							class="form-input code-input" 
							placeholder="ËØ∑ËæìÂÖ•È™åËØÅÁ†Å"
							maxlength="6"
						/>
						<view 
							class="code-btn" 
							:class="{ disabled: !canSendCode }" 
							@click="sendCode"
						>
							<text>{{ codeText }}</text>
						</view>
					</view>
				</view>

				<view class="login-btn" @click="phoneLogin" :class="{ disabled: !canLogin }">
					<text>ÁôªÂΩï</text>
				</view>
			</view>

			<!-- ÂæÆ‰ø°ÁôªÂΩï -->
			<view v-if="loginType === 'wechat'" class="wechat-login">
				<view class="wechat-btn" @click="wechatLogin">
					<text class="wechat-icon">üü¢</text>
					<text>ÂæÆ‰ø°‰∏ÄÈîÆÁôªÂΩï</text>
				</view>
			</view>


			<!-- Áî®Êà∑ÂçèËÆÆ -->
			<view class="agreement">
				<view class="checkbox-wrapper" @click="toggleAgreement">
					<view class="checkbox" :class="{ checked: agreedToTerms }">
						<text v-if="agreedToTerms" class="check-icon">‚úì</text>
					</view>
				</view>
				<text class="agreement-text">
					ÊàëÂ∑≤ÈòÖËØªÂπ∂ÂêåÊÑè
					<text class="link" @click="openUserAgreement">„ÄäÁî®Êà∑ÂçèËÆÆ„Äã</text>
					Âíå
					<text class="link" @click="openPrivacyPolicy">„ÄäÈöêÁßÅÊîøÁ≠ñ„Äã</text>
				</text>
			</view>
		</view>
	</view>
</template>

<script setup>
import { ref, computed } from 'vue'

// Ëá™ÂÆö‰πâÂØºËà™Ê†èÈ´òÂ∫¶
const systemInfo = wx.getSystemInfoSync()
const menuButtonInfo = wx.getMenuButtonBoundingClientRect()
const customNavBarHeight = systemInfo.statusBarHeight + (menuButtonInfo.top - systemInfo.statusBarHeight)

// ÁôªÂΩïÁ±ªÂûã
const loginType = ref('phone')

// Ë°®ÂçïÊï∞ÊçÆ
const phone = ref('')
const code = ref('')

// È™åËØÅÁ†ÅÁõ∏ÂÖ≥
const canSendCode = computed(() => {
	return /^1[3-9]\d{9}$/.test(phone.value) && countdown.value === 0
})

const countdown = ref(0)
const codeText = computed(() => {
	return countdown.value > 0 ? `${countdown.value}s` : 'Ëé∑ÂèñÈ™åËØÅÁ†Å'
})

// ÁôªÂΩïÊåâÈíÆÁä∂ÊÄÅ
const canLogin = computed(() => {
	return phone.value.length === 11 && code.value.length === 6 && agreedToTerms.value
})

// Áî®Êà∑ÂçèËÆÆ
const agreedToTerms = ref(false)

// ÂàáÊç¢ÁôªÂΩïÁ±ªÂûã
const switchLoginType = (type) => {
	loginType.value = type
}

// ÂèëÈÄÅÈ™åËØÅÁ†Å
const sendCode = () => {
	if (!canSendCode.value) return
	
	uni.showToast({
		title: 'È™åËØÅÁ†ÅÂ∑≤ÂèëÈÄÅ',
		icon: 'success'
	})
	
	// ÂºÄÂßãÂÄíËÆ°Êó∂
	countdown.value = 60
	const timer = setInterval(() => {
		countdown.value--
		if (countdown.value <= 0) {
			clearInterval(timer)
		}
	}, 1000)
}

// ÊâãÊú∫Âè∑ÁôªÂΩï
const phoneLogin = () => {
	if (!canLogin.value) return
	
	uni.showLoading({
		title: 'ÁôªÂΩï‰∏≠...'
	})
	
	// Ê®°ÊãüÁôªÂΩïËØ∑Ê±Ç
	setTimeout(() => {
		uni.hideLoading()
		uni.showToast({
			title: 'ÁôªÂΩïÊàêÂäü',
			icon: 'success'
		})
		
		// Â≠òÂÇ®Áî®Êà∑‰ø°ÊÅØ
		uni.setStorageSync('isLoggedIn', true)
		uni.setStorageSync('userInfo', {
			phone: phone.value,
			nickname: 'ÂÆ†Áâ©Áà±Â•ΩËÄÖ',
			avatar: '../../static/default-avatar.png'
		})
		
		// Ë∑≥ËΩ¨Âà∞‰∏ä‰∏ÄÈ°µÊàñÈ¶ñÈ°µ
		setTimeout(() => {
			uni.switchTab({
				url: '/pages/home/index'
			})
		}, 1500)
	}, 2000)
}

// ÂæÆ‰ø°ÁôªÂΩï
const wechatLogin = () => {
	uni.showLoading({
		title: 'ÁôªÂΩï‰∏≠...'
	})
	
	// Ë∞ÉÁî®ÂæÆ‰ø°ÁôªÂΩï
	uni.login({
		provider: 'weixin',
		success: (loginRes) => {
			// Ëé∑ÂèñÁî®Êà∑‰ø°ÊÅØ
			uni.getUserInfo({
				provider: 'weixin',
				success: (infoRes) => {
					uni.hideLoading()
					uni.showToast({
						title: 'ÁôªÂΩïÊàêÂäü',
						icon: 'success'
					})
					
					// Â≠òÂÇ®Áî®Êà∑‰ø°ÊÅØ
					uni.setStorageSync('isLoggedIn', true)
					uni.setStorageSync('userInfo', {
						nickname: infoRes.userInfo.nickName,
						avatar: infoRes.userInfo.avatarUrl,
						openid: loginRes.code
					})
					
					setTimeout(() => {
						uni.switchTab({
							url: '/pages/home/index'
						})
					}, 1500)
				},
				fail: () => {
					uni.hideLoading()
					uni.showToast({
						title: 'Ëé∑ÂèñÁî®Êà∑‰ø°ÊÅØÂ§±Ë¥•',
						icon: 'none'
					})
				}
			})
		},
		fail: () => {
			uni.hideLoading()
			uni.showToast({
				title: 'ÂæÆ‰ø°ÁôªÂΩïÂ§±Ë¥•',
				icon: 'none'
			})
		}
	})
}

// ÂàáÊç¢ÂçèËÆÆÂêåÊÑèÁä∂ÊÄÅ
const toggleAgreement = () => {
	agreedToTerms.value = !agreedToTerms.value
}

// ÊâìÂºÄÁî®Êà∑ÂçèËÆÆ
const openUserAgreement = () => {
	uni.navigateTo({
		url: '/pages/my/agreement'
	})
}

// ÊâìÂºÄÈöêÁßÅÊîøÁ≠ñ
const openPrivacyPolicy = () => {
	uni.navigateTo({
		url: '/pages/my/privacy'
	})
}

// ËøîÂõû‰∏ä‰∏ÄÈ°µ
const goBack = () => {
	uni.navigateBack({
		delta: 1
	})
}
</script>

<style lang="scss" scoped>
